body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

#root {
  /* width: 100vw;
  min-height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center; */
  max-height: 110vh;
}

#wrapper {
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  position: fixed;
}

#main {
  width: 100%;
  height: 100%;
  /* background-color: black; */
}

#infos {
  position: absolute;
  top: 1em;
  left: 1em;
  color: black;
  font-weight: bold;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

#content:hover {
  cursor: pointer;
}

#content {
  font-family: "Odibee Sans";
  display: none;
  /* width: 100vw; */
  /* height: 100vh; */
  text-align: center;
  font-size: 1rem;
  /* font-size: 180%; */
  letter-spacing: 3px;
  /* background-color: rgba(0, 0, 0, 0.4); */
  color: wheat;
  /* font-family: "Odibee Sans", cursive; */
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  z-index: 999;
}

@media only screen and (max-width: 600px) {
  #content {
    font-size: 0.8rem;
  }
}
