.logo-container {
  width: 100%;
  padding-top: 94.86%;
  position: relative;
}

.logo-container img {
  position: absolute;
  top: 0;
  right: 0;
  width: 100%;
}

.modal-wrapper,
form,
.form-container {
  display: flex;
  justify-content: center;
  align-items: center;
}

.modal-wrapper .form-container {
  position: relative;
  overflow-y: auto;
}

.modal-wrapper form {
  width: calc(100vh / 100 * 47 - 30px);
  background-color: rgb(255, 115, 23);
  padding: 30px;
  border: 5px solid #e0dada;
  flex-direction: column;
  box-sizing: border-box;
}

.modal-wrapper form h1 {
  width: 100%;
  text-align: center;
  line-height: 1.05;
}

.modal-wrapper form input {
  font-size: 18px;
  padding-left: 1rem;
  width: 100%;
  margin-bottom: 10px;
  height: 32px;
  border-radius: 4px;
  border: 2px solid #0e0d0d;
}

.modal-wrapper form button {
  background-color: #0e0d0d;
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 16px;
  white-space: nowrap;
  font-family: iCiel Mijas;
  background-image: url(../assets/btn-play.png);
  background-size: cover;
  background-repeat: round;
  border-radius: 8px;
  font-size: 1.5rem;
  border: 2px solid;
  cursor: pointer;
  user-select: none;
}

@media screen and (max-width: 600px) {
  .modal-wrapper form {
    width: 80%;
  }
  .modal-wrapper h1 {
    font-size: 1.5rem;
  }
}

@media screen and (min-width: 800px) {
  .modal-wrapper form {
    border-radius: 0;
  }
  .modal-wrapper form input {
    height: 42px;
  }
}
