.modal-wrapper .canvas-wrapper {
  width: 100%;
  /* max-width: 45vh; */
  /* height: 90vh; */
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: white;
  position: relative;
}

.modal-wrapper .canvas-wrapper .infos {
  position: relative;
}

.modal-wrapper canvas {
  width: 100%;
  height: 100%;
  background-color: rgba(255, 114, 22, 1);
}

.modal-wrapper .close-button {
  position: absolute;
  border: none;
  top: 0.5em;
  right: 0.5em;
  background: none;
  cursor: pointer;
  font-size: 1.5em;
  color: white;
}
