@font-face {
  src: url("../font/Mijas-Ultra.otf") format(truetype);
  font-family: iCiel Mijas;
}

.container {
  justify-content: center;
}

#btn-play {
  width: 196px;
  height: 63px;
  background-image: url(../assets/btn-play.png);
  background-size: contain;
  background-repeat: no-repeat;
  border-radius: 6px;
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
}

